<template>

  <div class="app-container">
    <p></p>
    <el-form ref="form" :model="form" label-width="120px">

      <el-form-item label="" >
        <!---->
        <el-col :span="20">
          <el-input v-model="form.title"/>
        </el-col>
      </el-form-item>

      <el-form-item label="()">
        <el-col :span="20">
      <!---->
          <el-upload
      class="upload-file"
      drag
      accept="image/png,image/gif.image/jpg,image/jpeg"
      :limit=1
      list-type="picture-card"
      :action="doUpload()"
      :before-upload="beforeUpload"
      ref="newupload"
      multiple
      :auto-upload="false">
        <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
          </el-upload>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <el-input v-model="form.blief"/>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <!-- autosize  autosize -->
          <el-input v-model="form.text"
                    :autosize="{ minRows: 5, maxRows: 20}"
                    type="textarea"/>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {}
      }
    },
    methods: {
      onSubmit() {
        //
        this.$refs.newupload.submit();
        // if(this.form.title!=null && this.form.blief != null && this.form.text != null){
        //   this.$axios.post("/service/postRecommendation", {title: this.form.title, blief: this.form.blief, text: this.form.text})
        //     .then(data => {
        //       if (data.data.status === "2000") {
        //         this.$message('');
        //         this.form = { }
        //       } else {
        //         this.$message({
        //           message: data.data.message,
        //           type: 'error'
        //         });
        //       }
        //     }).catch(()=>{this.openError("");});
        // }else {
        //   this.openWarn("");
        // }

      },
      onCancel() {
        this.$message({
          message: '',
          type: 'warning'
        });
        this.form = {};
      },
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      doUpload(){
        // url
        return "/useless";
      },
      beforeUpload(file){
        if (file!=null && this.form.title!=null && this.form.blief!=null && this.form.text!=null ){
          let fd = new FormData();
          fd.append('file',file);//
          fd.append('title',this.form.title);//
          fd.append('blief',this.form.blief);//
          fd.append('text',this.form.text);//
            // 
            this.$axios.post('/service/uploadLecturevideo',fd).then(function(res){
              this.openWarn("");
            }).catch(
              // (e)=>{this.openError(e.toString());}
            );

        } else {
          this.openWarn("");
        }
        // action
        return false

      },
    }
  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
</style>




