<template>

  <div class="app-container">
    <p></p>
    <el-carousel :interval="5000" arrow="always">
      <el-carousel-item v-for="item in 4" :key="item">
        <h3>{{ item }}</h3>
      </el-carousel-item>
    </el-carousel>
    <el-form ref="form" :model="form" label-width="120px">

      <el-form-item label="" >
        <!---->
        <el-col :span="20">
          {{form.clientID}}
        </el-col>
      </el-form-item>

      <!--<el-form-item label="">-->
        <!--<el-col :span="20">-->
          <!--<el-input v-model="form.name"/>-->
        <!--</el-col>-->
      <!--</el-form-item>-->

      <!--<el-form-item label="">-->
        <!--<el-col :span="20">-->
          <!--&lt;!&ndash; autosize  autosize &ndash;&gt;-->
          <!--<el-input v-model="form.text"-->
                    <!--:autosize="{ minRows: 5, maxRows: 20}"-->
                    <!--type="textarea"/>-->
        <!--</el-col>-->
      <!--</el-form-item>-->
      <el-form-item label="">
        <el-col :span="20">
          <el-upload
            class="upload-file"
            drag
            accept=".doc, .docx"
            :limit=1
            list-type="text"
            :action="doUpload()"
            :before-upload="beforeUpload"
            ref="newupload"
            multiple
            :auto-upload="false">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">doc/docx<em></em></div>
          </el-upload>
        </el-col>
      </el-form-item>
      <p></p>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: { clientID: this.$route.query.client }
      }
    },
    created(){
    },
    methods: {
      onSubmit() {
        // if (this.form.account != null && this.form.name != null && this.form.text != null){
        //   this.$axios.post("/doctor/postReport", {account: this.form.account, name: this.form.name, result: this.form.text})
        //     .then(data => {
        //       if (data.data.status === "2000") {
        //         this.$message('');
        //         this.form = { }
        //       } else {
        //         this.openError(data.data.message);
        //       }
        //     }).catch(()=>{this.openError("");});
        // } else {
        //   this.openWarn('');
        // }
        //
        this.$refs.newupload.submit();
      },
      onCancel() {
        this.$message({
          message: '',
          type: 'warning'
        });
        this.form = {};
      },
      doUpload(){
        // url
        return "/useless";
      },
      beforeUpload(file){
        if (file!=null && this.form.clientID!=null ){
          let fd = new FormData();
          fd.append('file',file);//
          fd.append('clientID',this.form.clientID);//
          // 
          console.log(this.form.clientID)
          this.$axios.post('/doctor/postReport',fd).then(function(res){

          }).catch(
            (e)=>{console.log(e.toString());}
          );

        } else {
          this.$alert("", '', {
            confirmButtonText: '',
            callback: action => {
              // 
            }
          });
        }
        // action
        return false
      },

    }
  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>




